.homePage {
  width: 100%;
  height: 100%;
  font-family: sans-serif;
}

.FirstPage {
  height: 100vh;
  background: radial-gradient(ellipse at top, #126419, #000),
    radial-gradient(ellipse at bottom, #000000, rgb(0, 0, 0));
}
.landingPageCover__animation {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  opacity: 0.07;
}

.landingPage {
  width: 80%;
  height: calc(100vh - 4em);
  opacity: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0px auto;
  padding: 1em;
}

.logoContainer {
  margin-top: 100px;
  position: relative;
}
.main_img {
  width: 25vw;
  filter: drop-shadow(-9px 68px 127px #000000);
  box-shadow: 0px 4px 60px -1px rgba(0, 0, 0, 0.2);
}

.innovationIn {
  display: flex;
}
.innovation {
  display: flex;
  font-size: 44px;
  background: linear-gradient(to right, #ffffff, #b7b4b2, #ccd0d4, #4d4c47);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}

.O {
  width: 200px;
  border: 10px solid #d9d9d9;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  border-top-right-radius: 20px;
  border-top-left-radius: 20px;
  height: 44px;
  margin: 18px;
}
#vation {
  color: linear-gradient(to right, #d5d5d5, #9692a5);
}
#in {
  color: #fff;
  padding-top: 6vh;
  margin-left: 5px;
  font-size: 40px;
}
#agriculture {
  font-size: 120px;
  color: #2de230;
}
.landingText {
  font-size: 18px;
  width: 80%;
  color: #fff;
  text-align: center;
  line-height: 1.5;
}
.landingButtons {
  font-size: large;
}
#signUpButton {
  background: linear-gradient(89.89deg, #003805 0.06%, #467814 102.53%);
  border-radius: 13px;
}
#aboutUsButton {
  background: linear-gradient(
    113.23deg,
    rgb(54 48 48 / 76%) 34.99%,
    rgba(255, 255, 255, 0) 116.05%
  );
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  box-shadow: 0px 4px 60px -1px rgba(0, 0, 0, 0.2),
    inset 0px 4px 50px rgba(0, 0, 0, 0.25);
  border-radius: 19px;
}

/* expanded landing page */
.extendedPage {
  height: 100%;
  background: rgb(5, 79, 17);
  background: linear-gradient(
    135deg,
    rgba(5, 79, 17, 0.99) 9%,
    rgba(0, 0, 0, 1) 50%,
    rgba(5, 62, 15, 1) 93%
  );
}
.main_container {
  padding-top: 160px;
  margin: 0px auto;
  width: 85%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.containerDomain {
  width: 25%;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.containerWorkshops {
  width: 25%;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.containerHeading {
  font-size: 45px;
  margin: 10px 0px;
}
.containerText {
  font-size: 18px;
  font-weight: lighter;
  line-height: 1.5;
}
.learnMoreButton {
  background: linear-gradient(
    113.23deg,
    rgba(255, 255, 255, 0.3) 34.99%,
    rgba(255, 255, 255, 0) 116.05%
  );
  box-shadow: 0px 4px 60px -1px rgba(0, 0, 0, 0.2),
    inset 0px 4px 50px rgba(0, 0, 0, 0.25);
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  border-radius: 19px;
  border-radius: 29px;
}

/* trailer */

#trailer {
  font-size: 72px;
  color: #74eb76;
  margin: 30px 0px;
}
.trailer {
  padding-top: 7vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 50px;
  flex-direction: column;
}
.trailerBox {
  width: 60vw;
  height: 60vh;
  border: 6px solid #74eb76;
  display: flex;
  padding: 1%;
  justify-content: center;
  align-items: center;
  border-radius: 63px;
}
.trailerVid {
  width: 60vw;
  height: 60vh;
  border-radius: 60px;
}
/* sponsor */
#sponsor {
  text-align: center;
  margin-top: 30px;
  font-size: 72px;
}
.sponsor {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 75vh;
  flex-direction: column;
}
.sponsorImages {
  width: 100%;
  display: flex;
  height: 65vh;
  justify-content: space-around;
  align-items: center;
}
.prevSlide {
  margin-left: 7%;
  width: 22vw;
  height: 25vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.mainSlide {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40vh;
  box-shadow: 30px #2de230;
  box-shadow: 40px 40px 15px 0px rgba(45, 226, 48, NaN);
  width: 30vw;
  position: relative;
  overflow: hidden;
}
.nextSlide {
  overflow: hidden;
  margin-right: 7%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 22vw;
  height: 25vh;
}
/* animations */
@keyframes scale-up-center {
  0% {
    transform: scale(0.5);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.5);
  }
}
/* MEDIA QUERY */
@media only screen and (max-width: 1100px) {
  .main_img {
    width: 35vw;
    height: 45vh;
  }
  .innovation {
    font-size: 38px;
  }
  .O {
    height: 40px;
    margin-top: 16px;
  }
  #in {
    padding-top: 3.9vh;
  }
  #agriculture {
    font-size: 120px;
  }
  .landingText {
    font-size: 16px;
  }
  .containerText {
    font-size: 16px;
  }
  #sponsor {
    margin-top: 30px;
    font-size: 60px;
  }
  .sponsorImages {
    height: 45vh;
  }
  .prevSlide {
    width: 20vw;
    height: 20vh;
  }
  .mainSlide {
    height: 40vh;
    width: 30vw;
  }
  .nextSlide {
    width: 20vw;
    height: 20vh;
  }
}

@media only screen and (max-width: 900px) {
  #agriculture {
    font-size: 100px;
  }
  .containerText {
    font-size: 15px;
  }
  .sponsorImages {
    margin-top: 20px;
    height: 25vh;
    margin-bottom: 30px;
  }
  .prevSlide {
    width: 20vw;
    height: 20vh;
  }
  .mainSlide {
    height: 30vh;
    width: 30vw;
  }
  .nextSlide {
    width: 20vw;
    height: 20vh;
  }
  .sponsor {
    height: 60vh;
  }
}

@media only screen and (max-width: 780px) {
  .main_img {
    width: 50vw;
    height: 36vh;
  }
  .innovation {
    font-size: 30px;
  }
  .O {
    height: 25px;
    width: 150px;
    margin-top: 13px;
  }
  #in {
    padding-top: 2.1vh;
  }
  #agriculture {
    font-size: 85px;
  }
  .trailerBox {
    width: 85vw;
    height: 60vh;
  }
  .trailerVid {
    width: 85vw;
    height: 60vh;
  }
  .sponsor {
    height: 40vh;
  }
  .sponsorImages {
    height: 35vh;
  }
  .prevSlide {
    display: none;
  }
  .mainSlide {
    height: 40vh;
    width: 80vw;
  }
  .nextSlide {
    display: none;
  }
}

@media only screen and (max-width: 690px) {
  .main_img {
    width: 55vw;
    height: 38vh;
  }
  .innovation {
    font-size: 23px;
  }
  .O {
    border: 8px solid;
    height: 20px;
    width: 130px;
    margin-top: 10px;
  }
  #in {
    font-size: 28px;
    padding-top: 1.7vh;
  }
  #agriculture {
    font-size: 70px;
  }
  .landingText {
    font-size: 14px;
  }
  .main_container {
    width: 80%;
    flex-direction: column;
  }
  .containerDomain {
    width: 90%;
    margin-bottom: 70px;
  }
  .containerWorkshops {
    width: 90%;
  }
  .containerText {
    font-size: 16px;
  }
  .sponsor {
    height: 60vh;
  }
}

@media only screen and (max-width: 500px) {
  .main_img {
    width: 70vw;
    height: 30vh;
  }
  .innovationIn {
    flex-direction: column;
  }
  .innovation {
    font-size: 20px;
  }
  .O {
    height: 15px;
    width: 90px;
    margin-top: 10px;
  }

  #in {
    margin: 0px auto;
  }
  #agriculture {
    font-size: 48px;
    font-weight: 800;
    margin-bottom: 10px;
  }
  .landingText {
    font-size: 12px;
  }

  /* trailer */
  #trailer {
    font-size: 50px;
  }
  /* sponsor */
  .sponsor {
    height: 60vh;
  }
  #sponsor {
    margin-top: 0px;
    font-size: 40px;
  }
  .sponsorImages {
    height: 30vh;
  }
  .prevSlide {
    display: none;
  }
  .mainSlide {
    height: 40vh;
    width: 80vw;
  }
  .nextSlide {
    display: none;
  }
}
